/* jshint ignore:start */

define('matchhistory/config/environment', ['ember'], function(Ember) {
  var prefix = 'matchhistory';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("matchhistory/tests/test-helper");
} else {
  require("matchhistory/app")["default"].create({"name":"matchhistory","version":"0.0.0+c7b268c9"});
}

/* jshint ignore:end */
